.calculator {
    height: 320px;
    width: 235px;
    border-radius: 5px;
    overflow: hidden; /* Tudo que estiver para além da calculadora será escondido */
    /* background-color: red; */

    display: grid;
    grid-template-columns: repeat(4, 25%);
    grid-template-rows: 1fr 48px 48px 48px 48px 48px;
}