:root{
    /* Criando variáveis para serem usadas no botão e para aplicar elas eu uso a palavra reservada var */
    --bg-button: #f0f0f0;
    --border-button: solid 1px #888;
}

.button{
    font-size: 1.4em;
    background-color: var(--bg-button);
    border: none;
    border-right: var(--border-button);
    border-bottom: var(--border-button);
    outline: none;
}

.button:active {
    background-color: #ccc;
}

.button.double { /* Botão ocupando duas posições na grid */
    grid-column: span 2
}

.button.triple { /* Botão ocupando três posições na grid */
    grid-column: span 3
}

.button.operation { /* Botão representando uma operação */
    background-color: #fa8231;
    color: #fff;
}

.button.operation:active{
    background-color: #fa8231cc;
}